import React, { Component } from 'react';
import queryString from 'query-string';
import TopBar from '../TopBar';
import LongButton from '../LongButton';
import { Dialog } from '../Dialog';
import classnames from 'classnames';
import { sendCode, changePhone } from '../../../../../action';
import { CONST_CODE } from '../../../../../constant';
import { encrypt } from '../../../../../util/encryptParams';
import { ToutiaoJSBridgePromise } from '../../../../../util/common';

import './index.less';

const aid = CONST_CODE && CONST_CODE.HUOSHAN_AID || 0;

class NewPhoneVerify extends Component {
    constructor(props) {
        super(props);
        const params = queryString.parse(props.location && props.location.search);
        this.oldPhone = params.oldPhone || '';
        this.ticket = params.ticket || '';
        this.authToken = params.authToken || '';
        this.areaCode = '86';

        this.appInfo = {};
        this.iptRef = null;
        this.state = {
            sending: false,
            seconds: 59,
            code: '', //验证码
            showDialog: false,
            dialogTitle: '',
            closePage: false,
            phone: '',
            phoneValid: false,
            verifyValid: false
        };
    }

    getAppInfo = () => {
        return ToutiaoJSBridgePromise('appInfo').then(appInfo => {
            this.appInfo = appInfo || {};
        });
    }

    sendVerifyCode = (e) => {
        e && e.stopPropagation();
        const { sending, phoneValid } = this.state;
        if (!sending && phoneValid) {
            this.beginCount();
            const mobile = encrypt(`+${this.areaCode}${this.state.phone}`);
            sendCode({
                mix_mode: 1,
                type: encrypt(20),
                unbind_exist: 0,
                mobile,
                old_mobile: this.oldPhone,
                unusable_mobile_ticket: this.ticket,
                auth_token: this.authToken,
                device_id: this.appInfo.device_id || 0,
                aid,
            }).then(data => {
                console.log({ data });
            }).catch(errMsg => {
                this.setState({
                    showDialog: true,
                    dialogTitle: errMsg || '发送短信失败，请稍后再试'
                });
            })
        }
    }

    beginCount = () => {
        this.setState({
            sending: true,
            seconds: 59
        });
        const interval = setInterval(() => {
            let seconds = this.state.seconds - 1;
            if (seconds >= 0) {
                this.setState({
                    seconds
                });
            } else {
                this.setState({
                    sending: false
                });
                clearInterval(interval);
            }
        }, 1000);
    }

    codeChangeHd = (e) => {
        e.stopPropagation();
        let code = e.target.value;
        code = code.slice(0, 4);
        const verifyValid = code.length === 4 ? true : false;
        this.setState({
            code,
            verifyValid
        });
    }

    phoneChangeHd = e => {
        let phone = e.target.value || '';
        phone = phone.replace(/\s/g, '');
        const phoneValid = this.checkPhone(phone);
        phone = phone.replace(/^(\d{0,3})(\d{1,4})?(\d{1,})?$/, function (m, p1, p2, p3) {
            let str = p1 || '';
            p2 && (str += ' ' + p2);
            p3 && (str += ' ' + p3);
            return str;
        });

        this.setState({
            phone,
            phoneValid
        });
        this.setCaretPosition(this.iptRef, phone.length);
    }

    setCaretPosition(iptRef, len) { // 设置光标位置
        if (iptRef && iptRef.createTextRange) {
            const rg = iptRef.createTextRange();
            rg.collapse(false);
            rg.select();
        } else if (iptRef && iptRef.setSelectionRange) {
            setTimeout(function () {
                iptRef.setSelectionRange(len, len);
            }, 20);
        }
    }

    checkPhone = phone => {
        const { areaCode } = this;
        if (areaCode == '86') {
            return phone && phone.length === 11;
        } else {
            return areaCode != '0' && phone && phone.length >= 10;
        }
    }

    goBack = () => {
        const { history } = this.props;
        history.goBack();
    }

    nextClickHd = () => {
        const { verifyValid, code } = this.state;
        if (!verifyValid) return;
        const mobile = encrypt(`+${this.areaCode}${this.state.phone}`);
        changePhone({
            mix_mode: 1,
            mobile,
            code: encrypt(code),
            unusable_mobile_ticket: this.ticket,
            auth_token: this.authToken,
            device_id: this.appInfo.device_id || 0,
            aid,
        }).then(data => {
            this.setState({
                showDialog: true,
                dialogTitle: '换绑成功',
                closePage: true
            });
        }).catch(errMsg => {
            this.setState({
                showDialog: true,
                dialogTitle: errMsg || '换绑失败，请稍后再试'
            });
        })
    }

    confirmDialog = () => {
        this.setState({
            showDialog: false
        });
        if (this.state.closePage) {
            ToutiaoJSBridgePromise('rebindPhoneSucceed');
        }
    }

    componentDidMount() {
        this.getAppInfo();
        // this.beginCount();
    }

    render() {
        const { sending, seconds, code, phoneValid, showDialog, dialogTitle, phone, verifyValid } = this.state;
        const sendText = sending ? `重新发送(${seconds})` : '获取验证码';
        return (
            <div className="new-phone-verify">
                <TopBar title={'验证新手机号'} iconClickHd={this.goBack} />
                <div className="content">
                    <div className="form-wrap">
                        <div className="phone-wrap">
                            <div className="left">
                                <div className="label">手机号</div>
                                <input className="ipt" type="tel" ref={ref => { this.iptRef = ref }} value={phone} onChange={this.phoneChangeHd} placeholder="请输入手机号" />
                            </div>
                            <div className={classnames('right', { disabled: sending || !phoneValid })} onClick={this.sendVerifyCode}>{sendText}</div>
                        </div>
                        <div className="code-wrap">
                            <div className="label">验证码</div>
                            <input className="ipt" type="number" value={code} onChange={this.codeChangeHd} placeholder="请输入验证码" />
                        </div>
                    </div>
                    <LongButton text={'下一步'} disabled={!verifyValid} clickHd={this.nextClickHd} />
                    {!!showDialog && <Dialog title={dialogTitle} confirmFn={this.confirmDialog} />}
                </div>
            </div>
        )
    }
}

export default NewPhoneVerify;


