/**
 * 页面相关请求
 *
 */

/* eslint-disable */
const __DEV__ = process.env.NODE_ENV === 'development';
/* eslint-enable */

const API_PREFIX_CONFIG = {
  PASSPORT: __DEV__ ? '/passport' : '/passport',
  LOCK_API: __DEV__ ? '/passport/safe/api/v1' : '/passport/safe/api/v1',
  SSO_API: __DEV__ ? '/passport/web' : '/passport/web',
};

const getApiAddress = (key, path, version) => {
  const url = API_PREFIX_CONFIG[key];
  if (version) {
    return `${url}/${path}/v${version}`;
  }
  return `${url}/${path}`;
};

export default getApiAddress;



