import React, { Component } from 'react';
import { Route } from "react-router-dom"; //HashRouter,BrowserRouter

import RouterWrap from '../../../components/RouterWrap';
import ThirdAppAuthorize from './components/ThirdAppAuthorize';
import AuthorizeFail from './components/AuthorizeFail';
import PasswordAuthorize from './components/PasswordAuthorize';
import NewPhoneVerify from './components/NewPhoneVerify';
import { authAvailableWays } from '../../../action';
import { CONST_CODE } from '../../../constant';
import { ToutiaoJSBridgePromise } from '../../../util/common';

import './index.less';

const aid = CONST_CODE && CONST_CODE.HUOSHAN_AID || 0;

class ChangeBinding extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <RouterWrap>
                <Route exact path="/new-phone-verify" component={NewPhoneVerify} />
                <Route exact path="/" component={Untrusted} />
                <Route exact path="/authorize-fail" component={AuthorizeFail} />
            </RouterWrap>
        )
    }
}

class SubmitMaterials extends Component {
    constructor(props) {
        super(props);
        if (window) {
            window.location.href = window.location.protocol + '//hotsoon.snssdk.com/falcon/live_inapp/page/feedback/index.html#/';
        }
    }

    render() {
        return null
    }
}

class Untrusted extends Component {
    constructor(props) {
        super(props);
        this.state = {
            status: 0,
            oauth_platforms: []
        }
    }

    componentWillMount() {
        ToutiaoJSBridgePromise('appInfo').then(appInfo => {
            if (!appInfo) return Promise.reject('appInfo is null');
            return authAvailableWays({
                device_id: appInfo.device_id || 0,
                aid
            });
        }).then(data => {
            if (data) {
                if (data.oauth_platforms && data.oauth_platforms.length) {
                    this.setState({
                        status: 1, //1
                        oauth_platforms: data.oauth_platforms
                    })
                } else if (data.has_pwd) {
                    this.setState({
                        status: 2 //2
                    })
                } else {
                    this.setState({
                        status: 3 //3
                    })
                }
            }
        }).catch(err => {
            console.log({ 'appInfo err': err });
        })
        // todo: delete
        setTimeout(() => {
            if (this.state.status === 0) {
                this.setState({
                    status: 3
                });
            }
        }, 5000);
    }

    render() {
        const { status, oauth_platforms } = this.state;
        if (status === 1) {
            return <ThirdAppAuthorize {...this.props} oauth_platforms={oauth_platforms || []} />;
        } else if (status === 2) {
            return <PasswordAuthorize {...this.props} />;
        } else if (status === 3) {
            return <SubmitMaterials {...this.props} />;
        } else {
            return null;
        }
    }
}

export default ChangeBinding;


