import { call } from '@bridge/byted-bridge-app-jssdk';

export function ToutiaoJSBridgePromise(name, param = {}) {
  return new Promise((resolve, reject) => {
    if (!window) reject('window is undefined');
    /* eslint-disable */
    window &&
      window.ToutiaoJSBridge.call(name, param, resp => {
        resolve(resp);
      });
  });
  /* eslint-enable */
}

export function toutiaoAppJSBridgePromise(name, param = {}) {
  return new Promise((resolve, reject) => {
    if (!call) {
      reject('byted-bridge-app-jssdk call is undefined');
      throw new Error('byted-bridge-app-jssdk call is undefined');
    }
    call(name, param, (resp) => {
      if (resp) {
        resolve(resp);
      } else {
        reject('byted-bridge-app-jssdk call is null');
      }
    });
  });
}

export default {};



