import React from 'react';
import classnames from 'classnames';
import './index.less';

const TopBar = ({ ...props }) => {
    const { iconClickHd, title = '', showIcon = true, iconClass = 'back' } = props
    return (
        <div className="top-bar">
            {!!showIcon && <div className={classnames('top-bar-back-icon', iconClass)} onClick={e => { e.stopPropagation(); iconClickHd && iconClickHd() }}></div>}
            <div className="top-bar-title">{title}</div>
        </div>
    )
}

export default TopBar;


