/**
 * 页面相关请求
 *
 */

import { Request } from '../util';
import getApiAddress from './config';
import { ToutiaoJSBridgePromise } from '../util/common';

let lockTicket = null;
let unlockTicket = null;

const isMock = false;

const urlPrefix =
  (window && `${window.location.protocol}//${window.location.hostname}`) || '';

const RequestUrl = {
  sendCode: getApiAddress('PASSPORT', 'mobile/send_code', 1),
  checkUnusable: getApiAddress('PASSPORT', 'mobile/check_unusable'),

  // /api/v1/lock/request
  sliderRegister: getApiAddress('LOCK_API', 'slider/register'),
  lockQuery: getApiAddress('LOCK_API', 'lock/request'),
  unlockQuery: getApiAddress('LOCK_API', 'unlock/request'),
  lock: getApiAddress('LOCK_API', 'lock/confirm'),
  unlock: getApiAddress('LOCK_API', 'unlock/confirm'),

  changePhone: getApiAddress('PASSPORT', 'mobile/change', 1),
  passwordAuthorize: getApiAddress('PASSPORT', 'mobile/authorize'),
  thirdAuthorize: getApiAddress('PASSPORT', 'auth/authorize'),
  authAvailableWays: getApiAddress('PASSPORT', 'auth/available_ways'),

  // 绑定冲突
  conflictUserInfo: getApiAddress('PASSPORT', 'mobile/conflict_user_info'),
  userWorth: getApiAddress('PASSPORT', 'safe/get_user_worth'),
  cancelUser: getApiAddress('PASSPORT', 'cancel/quick_cancel'),
};

export const sliderRegister = params =>
  Request.post(RequestUrl.sliderRegister, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  }).then(res => res);

export const lockQuery = (params) => {
  lockTicket = null;
  return Request.post(RequestUrl.lockQuery, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {
        ticket_lock: 'abcdefg1234567',
      },
    }, // mock 返回值
  }).then((res) => {
    lockTicket = res.ticket_lock;
    return res;
  });
};

export const lockAccount = params =>
  Request.post(
    RequestUrl.lock,
    { ...params, ticket_lock: lockTicket },
    {
      isMock, // mock
      response: {
        message: 'error',
        data: {
          error_code: 1001,
          description: '暂不支持您所使用的APP',
        },
      }, // mock 返回值
    },
  );

export const unlockQuery = (params) => {
  unlockTicket = null;
  return Request.post(RequestUrl.unlockQuery, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  }).then((res) => {
    unlockTicket = res.ticket_unlock;
    return res;
  });
};

export const unlockAccount = params =>
  Request.post(
    RequestUrl.unlock,
    { ...params, ticket_unlock: unlockTicket },
    {
      isMock, // mock
      response: {
        message: 'success',
        data: {
          error_code: 1001,
          description: '暂不支持您所使用的APP',
        },
      }, // mock 返回值
    },
  );

export const checkUnusable = params =>
  Request.get(RequestUrl.checkUnusable, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  });

function JSBridgeFetch({ url, method = 'GET', params }) {
  return ToutiaoJSBridgePromise('fetch', {
    url: urlPrefix + url,
    method,
    params,
  }).then((res) => {
    // 处理ios和android返回的数据格式不一致
    const resp =
      res && res.response ? res.response : res && res.raw ? res.raw : res;
    if (resp && resp.message === 'success') {
      return Promise.resolve(resp.data);
    }
    const errMsg = resp
      ? resp.message != 'error'
        ? resp.message
        : (resp.data && resp.data.description) || ''
      : '';
    return Promise.reject(errMsg);
  });
}

export const sendCode = params =>
  JSBridgeFetch({ url: RequestUrl.sendCode, params });

export const changePhone = params =>
  JSBridgeFetch({ url: RequestUrl.changePhone, params });

export const passwordAuthorize = params =>
  JSBridgeFetch({ url: RequestUrl.passwordAuthorize, params });

export const thirdAuthorize = params =>
  JSBridgeFetch({ url: RequestUrl.thirdAuthorize, params });

export const authAvailableWays = params =>
  JSBridgeFetch({ url: RequestUrl.authAvailableWays, params });

// export const conflictUserInfo = params =>
//   JSBridgeFetch({ url: RequestUrl.authAvailableWays, params });

export const conflictUserInfo = params =>
  Request.get(RequestUrl.conflictUserInfo, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {
        user_id: 52401424290,
        nick_name: 'Mr.Chow',
        avatar_url: 'http://p1.pstatp.com/thumb/7b93000a1ec8734666d1',
        user_sign: '838517280',
      },
    }, // mock 返回值
  });

export const sendCodeByH5 = params =>
  Request.get(RequestUrl.sendCode, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  });

export const userWorth = params =>
  Request.get(RequestUrl.userWorth, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  });

export const cancelUser = params =>
  Request.get(RequestUrl.cancelUser, params, {
    isMock, // mock
    response: {
      message: 'success',
      data: {},
    }, // mock 返回值
  });

export default RequestUrl;



