import React, { Component } from 'react';
import queryString from 'query-string';
import TopBar from '../TopBar';
import { Dialog } from '../Dialog';
import LongButton from '../LongButton';

import { CONST_CODE } from '../../../../../constant';
import { passwordAuthorize } from '../../../../../action';
import { ToutiaoJSBridgePromise } from '../../../../../util/common';

import './index.less';

const aid = CONST_CODE && CONST_CODE.HUOSHAN_AID || 0;

class PasswordAuthorize extends Component {
    constructor(props) {
        super(props);
        this.appInfo = {};
        const params = queryString.parse(window.location && window.location.search);
        this.phone = params.phone || '';
        this.ticket = params.ticket || '';
        this.state = {
            password: '',
            valid: false,
            count: 0,
            showDialog: false,
            dialogTitle: ' '
        }
    }

    getAppInfo = () => {
        ToutiaoJSBridgePromise('appInfo').then(data => {
            this.appInfo = data || {};
        });
    }

    pwdChangeHd = e => {
        e.stopPropagation();
        const password = e.target.value;
        const valid = password && password.length >= 8 && password.length <= 20 ? true : false;
        this.setState({
            password,
            valid
        });
    }

    nextClickHd = () => {
        const { valid, password } = this.state;
        if (!valid) return;
        passwordAuthorize({
            mix_mode: 1,
            mobile: this.phone,
            password,
            device_id: this.appInfo.device_id || 0,
            aid,
        }).then(data => {
            const authToken = data && data.token || '';
            if (!authToken) return;
            const { history } = this.props;
            history.push({
                pathname: '/new-phone-verify',
                search: `?oldPhone=${this.phone}&ticket=${this.ticket}&authToken=${authToken}`,
                state: { transition: 'slide-left' }
            });
        }).catch(errMsg => {
            const count = this.state.count;
            if (count < 5) {
                this.setState({
                    count: count + 1,
                    showDialog: true,
                    dialogTitle: errMsg || '验证失败，请重试'
                });
            } else {
                const { history } = this.props;
                history.push({
                    pathname: '/authorize-fail',
                    search: ``,
                    state: { transition: 'slide-left' }
                });
            }

        })
    }

    goBack = () => {
        ToutiaoJSBridgePromise('close');
    }

    confirmDialog = () => {
        this.setState({
            showDialog: false
        });
    }

    componentDidMount() {
        this.getAppInfo();
    }

    render() {
        const { valid, password, showDialog, dialogTitle } = this.state;
        return (
            <div className="password-authorize">
                <TopBar title={'登陆密码验证'} iconClickHd={this.goBack} />
                <div className="content">
                    <div className="password-desc">帐号设置了登陆密码，请通过登陆密码进行身份验证，验证通过后可以换绑</div>
                    <div className="form-wrap">
                        <div className="label">验证</div>
                        <input type="password" className="ipt" placeholder="请输入登陆密码进行校验" value={password} onChange={this.pwdChangeHd} />
                    </div>
                    <LongButton clickHd={this.nextClickHd} text={'下一步'} disabled={!valid} />
                </div>
                {!!showDialog && <Dialog title={dialogTitle} confirmFn={this.confirmDialog} />}
            </div>
        )
    }
}

export default PasswordAuthorize;


