// https://github.com/ReactTraining/history
// https://medium.com/lalilo/dynamic-transitions-with-react-router-and-react-transition-group-69ab795815c9
// https://github.com/reactjs/react-transition-group

import React, { Component } from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import { createHashHistory } from 'history';
import { TransitionGroup, CSSTransition } from 'react-transition-group';

import './index.less';

class RouterWrap extends Component {
  constructor(props) {
    super(props);
    this.history = createHashHistory({ ...this.props, hashType: 'noslash' });
  }

  render() {
    return (
      <Router history={this.history}>
        <Route
          render={({ location }) => {
            const { pathname, state } = location;
            return (
              <TransitionGroup
                childFactory={child =>
                  React.cloneElement(child, {
                    classNames: (state && state.transition) || 'slide-right',
                    timeout: 500,
                  })
                }
              >
                <CSSTransition key={pathname} timeout={500}>
                  <Switch location={location}>{this.props.children}</Switch>
                </CSSTransition>
              </TransitionGroup>
            );
          }}
        />
      </Router>
    );
  }
}

export default RouterWrap;



