
/**
 * 得到UTF8编码的内容
 * @param string str
 */
function writeUTF(str) {
    var back = [];
    var code, i = 0, j = 0;
    for (i = 0; i < str.length; i++) {
        code = str.charCodeAt(i);
        if (0x00 <= code && code <= 0x7f) {
            back.push(code);
        } else if (0x80 <= code && code <= 0x7ff) {
            back.push(192 | (31 & (code >> 6)));
            back.push(128 | (63 & code));
        } else if (
            (0x800 <= code && code <= 0xd7ff)
            || (0xe000 <= code && code <= 0xffff)
        ) {
            back.push(224 | (15 & (code >> 12)));
            back.push(128 | (63 & (code >> 6)));
            back.push(128 | (63 & code));
        }
    }
    for (j = 0; j < back.length; j++) {
        back[j] &= 0xff;
    }
    return back;
}

export function encrypt(value) {
    var i, length, result = [], newCode = [];
    if (value === void 0) {
        return '';
    }
    value += '';
    newCode = writeUTF(value);
    for (i = 0, length = newCode.length; i < length; ++i) {
        result.push((newCode[i] ^ 5).toString(16));
    }
    return result.join('');
};

export default function encryptParams(params, keyList) {
    var i, length, value, mix_mode = 0;
    if (typeof params !== 'object') {
        return params;
    }
    params = Object.assign({}, params);
    keyList = keyList || [];
    for (i = 0, length = keyList.length; i < length; ++i) {
        value = params[keyList[i]];
        if (value !== void 0) {
            mix_mode |= 1;
            params[keyList[i]] = this.__encrypt(value);
        }
    }
    params.mix_mode = mix_mode;
    return params;
};


