import React, { Component } from 'react';
import queryString from 'query-string';
import TopBar from '../TopBar';
import { Dialog } from '../Dialog';
import classnames from 'classnames';

import { CONST_CODE } from '../../../../../constant';
import { thirdAuthorize } from '../../../../../action';
import { ToutiaoJSBridgePromise } from '../../../../../util/common';

const aid = CONST_CODE && CONST_CODE.HUOSHAN_AID || 0;

import './index.less';

const AuthrizeItem = ({ ...props }) => {
    const { iconClass = '', name = '', platform = '', clickHd } = props;
    return (
        <div className="authorize-item" onClick={e => { e.stopPropagation(); clickHd && clickHd(platform) }} >
            <div className="item-wrap">
                <div className="left">
                    <div className={classnames('app-icon', iconClass)}></div>
                    <div className="name">{name}</div>
                </div>
                <div className="right">
                    <div className="desc">去验证</div>
                    <div className="arrow-icon"></div>
                </div>
            </div>
        </div>
    )
}

const items = [
    { iconClass: 'weixin', name: '微信', platform: 'weixin' },
    { iconClass: 'qq', name: 'QQ', platform: 'qzone_sns' },
    { iconClass: 'weibo', name: '微博', platform: 'sina_weibo' },
    { iconClass: 'toutiao', name: '今日头条', platform: 'toutiao' },
];

const androidPkgs = {
    'weixin': 'com.tencent.mm',
    'qzone_sns': 'com.tencent.mobileqq',
    'sina_weibo': 'com.sina.weibo',
    'toutiao': 'com.ss.android.article.news'
}

const adroidPkgToApps = {
    'com.tencent.mm': 'weixin',
    'com.tencent.mobileqq': 'qzone_sns',
    'com.sina.weibo': 'sina_weibo',
    'com.ss.android.article.news': 'toutiao'
}

const platformIds = {
    'weixin': 81,
    'qzone_sns': 82,
    'sina_weibo': 83,
    'toutiao': 151,
    // 'huawei': 225,
    // 'sina_weibo_wap': 232,
    // 'toutiao_v2': 414
}

class ThirdAppAuthorize extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showDialog: false,
            dialogTitle: '当前设备上没有安装微信，无法完成校验。请安装该APP后重试。',
        };
        this.appInfo = {};
        this.apiParam = {};
        this.installedApps = [];
        // 由于hash问题，iphone的props.location.search有问题，所以取windows的，其它子页面仍然用props.location.search
        const params = queryString.parse(window.location && window.location.search);
        this.phone = params.phone || '';
        this.ticket = params.ticket || '';
        const { oauth_platforms } = this.props;
        this.showItems = items.filter(item => oauth_platforms.includes(item.platform));
    }

    getAppInfo = () => {
        ToutiaoJSBridgePromise('appInfo').then(appInfo => {
            this.appInfo = appInfo || {};
        });
    }

    getInstalledThirdApps() {
        const { oauth_platforms } = this.props;
        ToutiaoJSBridgePromise('apiParam').then(apiParam => {
            let app_ids = oauth_platforms;
            this.apiParam = apiParam;
            if (apiParam && apiParam.device_platform === 'android') {
                app_ids = oauth_platforms.map(v => androidPkgs[v]);
            }
            return ToutiaoJSBridgePromise('checkInstalledApps', { app_ids });
        }).then(resp => {
            let installedApps = resp && resp.response;
            if (this.apiParam && this.apiParam.device_platform === 'android') {
                installedApps = (installedApps || []).map(v => adroidPkgToApps[v]);
            }
            this.installedApps = installedApps || [];
        }).catch(errMsg => {
            console.log({ errMsg });
        });
    }

    authorize = ({ platform = '', code = '', openid = '', access_token = '', expires_in = '' }) => {
        const platform_app_id = platformIds[platform] || '';
        thirdAuthorize({
            code,
            access_token: access_token,
            access_token_secret: access_token,
            expires_in,
            platform,
            platform_app_id,
            device_id: this.appInfo.device_id || 0,
            aid,
        }).then(data => {
            const authToken = data && data.token || '';
            if (!authToken) return;
            const { history } = this.props;
            history.push({
                pathname: '/new-phone-verify',
                search: `?oldPhone=${this.phone}&ticket=${this.ticket}&authToken=${authToken}`,
                state: { transition: 'slide-left' }
            });
        }).catch(errMsg => {
            const { history } = this.props;
            history.push({
                pathname: '/authorize-fail',
                search: ``,
                state: { transition: 'slide-left' }
            });
        })
    }

    listClickHd = (platform) => {
        this.thirdAuthorizeBridge(platform);
    }

    goNext = () => {
        const { history } = this.props;
        history.push({
            pathname: '/',
            search: `?from=h5`,
            state: { transition: 'slide-left' }
        });
    }

    thirdAuthorizeBridge = (platform) => {
        if (!this.installedApps.includes(platform)) {
            let appName = '';
            this.showItems.forEach(item => {
                if (item.platform === platform) {
                    appName = item.name
                }
            });
            const dialogTitle = `当前设备上没有安装${appName}，无法完成校验。请安装该APP后重试。`;
            this.setState({
                dialogTitle,
                showDialog: true
            });
            return;
        }
        ToutiaoJSBridgePromise('thirdPartyAuth', { platform }).then(resp => {
            if (resp && resp.response) {
                this.authorize(resp.response);
            }
        })
    }

    closeDialog = () => {
        this.setState({
            showDialog: false
        })
    }

    confirmDialog = () => {
        this.setState({
            showDialog: false
        })
    }

    goBack = () => {
        ToutiaoJSBridgePromise('close');
    }

    componentDidMount() {
        this.getAppInfo();
        this.getInstalledThirdApps();

    }

    render() {
        const { showItems } = this;
        const { showDialog, dialogTitle } = this.state;
        return (
            <div className="third-app-authorize">
                <TopBar title={'第三方授权验证'} iconClickHd={this.goBack} />
                <div className="content">
                    <div className="desc-text">帐号绑定了第三方，请通过第三方授权的方式进行身份验证，验证通过后可以换绑</div>
                    <div className="authorize-list">
                        {
                            showItems.map((item, k) => <AuthrizeItem key={k} clickHd={this.listClickHd} {...item} />)
                        }
                    </div>
                    {showDialog && <Dialog title={dialogTitle} confirmFn={this.confirmDialog} ></Dialog>}
                </div>
            </div>
        )
    }
}

export default ThirdAppAuthorize;


