import React from 'react';
import './index.less'

function DialogWrap(props) {
    const { children } = props;
    return (
        <div className="custom-dialog-wrap">
            <div className="custom-dialog">
                {children}
            </div>
        </div>
    )
}

export function Dialog(props) {
    const { title = '', desc = '', confirmText = '确定', confirmFn } = props;
    return (
        <div className="custom-dialog-wrap">
            <div className="custom-dialog">
                {!!title && <div className="custom-dialog-title">{title}</div>}
                {!!desc && <div className="custom-dialog-desc">{desc}</div>}
                <div className="custom-dialog-footer">
                    {!!confirmText && <div className="custom-dialog-confirm" onClick={e => { e.stopPropagation(); confirmFn && confirmFn() }}>{confirmText}</div>}
                </div>
            </div>
        </div>
    )
};

export default DialogWrap;


