/**
 * @providesModule ByteUtil
 * 一些常用的工具类
 */

export { default as Request } from './Request/Request';
export { default as iToast } from './Toast';

export const isMobile = value => /^1\d{10}$/.test(value);

export const webviewDestroy = () => {
  window.ToutiaoJSBridge.call('close');
};

export const loadScript = (url) => {
  const script = document.createElement('script');
  script.type = 'text/javascript';
  if (script.readyState) {
    // IE
    script.onreadystatechange = function () {
      if (script.readyState === 'loaded' || script.readyState === 'complete') {
        script.onreadystatechange = null;
      }
    };
  } else {
    // Others
    script.onload = function () {};
  }
  script.src = url;
  document.getElementsByTagName('head')[0].appendChild(script);
};

export default {};



