import { Toast } from 'antd-mobile';

const iToast = {
  show: (text = '') => {
    Toast.info(text, 3, null, false);
  },
  loading: (content = '', duration = 3, onClose = () => {}) => {
    Toast.loading(content, duration, onClose, true);
  },
  hide: () => {
    Toast.hide();
  },
};

export default iToast;



