import React, { Component } from 'react';
import LongButton from '../LongButton';
import TopBar from '../TopBar';
import { ToutiaoJSBridgePromise } from '../../../../../util/common';

import './index.less';

class AuthorizeFail extends Component {
    constructor(props) {
        super(props)
    }

    exit = () => {
        ToutiaoJSBridgePromise('rebindPhoneSucceed');
    }

    // goBack = () => {
    //     const { history } = this.props;
    //     history.goBack();
    // }

    render() {
        return (
            <div className="authorize-fail">
                <TopBar title={'验证失败'} iconClickHd={this.exit} />
                <div className="content">
                    <div className="img-fail"><div className="icon"></div></div>
                    <div className="fail-result">验证不通过，不能换绑</div>
                    <LongButton clickHd={this.exit} text={'退出'} disabled={false} />
                </div>
            </div>
        )
    }
}

export default AuthorizeFail;


