import { Toast } from 'antd-mobile';
import axios from 'axios';

const API_HOST = '';

const HEADERS = {
  'Content-Type': 'application/x-www-form-urlencoded',
  Accept: 'application/json, text/javascript',
};

const request = (method, url, requestParams = {}, opts = {}) => {
  const requestUrl = `${API_HOST}${url}`;
  if (opts.isMock) {
    /* eslint-disable */
    console.log('isMock:', requestUrl, requestParams, opts.response || {});
    /* eslint-enable */
    const { data, message } = opts.response;
    if (message === 'success') {
      return Promise.resolve(data);
    }
    if (opts.response.error_code === 0) {
      return Promise.resolve(data);
    }
    return Promise.reject(data);
  }

  return new Promise((resolve, reject) => {
    let secondParams = {};
    let thirdParams = {};
    const config = {};
    if (opts.headers) {
      config.headers = {
        ...HEADERS,
        ...opts.headers,
      };
    }
    if (method === 'get') {
      secondParams = {
        params: requestParams,
        ...config,
      };
    } else if (method === 'post') {
      secondParams = requestParams;
      thirdParams = config;
    }
    // axios#get(url[,config])   axios#post(url[,data[,config]])
    axios[method](requestUrl, secondParams, thirdParams)
      .then(res => {
        const responseData = res.data;
        const { data, message } = responseData;
        // error_code 返回方式
        const errorCode = responseData.error_code;
        if (errorCode === 0) {
          resolve(data || {});
        } else if (errorCode && errorCode !== 0) {
          reject(responseData);
        } else if (message && message === 'success') {
          resolve(data || {});
        } else if (message && message !== 'success') {
          reject(data);
        } else {
          reject(responseData);
        }
      })
      .catch(err => {
        if (err.response && err.response.status === 500) {
          Toast.fail('服务器异常，请稍后重试');
        } else if (err.response && err.response.status === 400) {
          Toast.fail('参数异常，请确定参数是否正确');
        } else {
          /* eslint-disable */
          console.error(`${requestUrl}：${err}`);
          /* eslint-enable */
        }
        reject(err);
      });
  });
};

function get(url, params, opts) {
  return request('get', url, params, opts);
}

function post(url, params, opts) {
  return request('post', url, params, opts);
}

const Request = {
  get,
  post,
};

export default Request;



