import React from 'react';
import classnames from 'classnames';
import './index.less';

const LongButton = ({ ...props }) => {
    const { clickHd, text = '', disabled = false } = props
    return (
        <div className={classnames('long-button', { disabled })} onClick={e => { e.stopPropagation(); clickHd && clickHd() }}>{text}</div>
    )
}

export default LongButton;


